<template>
  <div class="messages full-screen">
    <div class="page-panel">
      <pem-scroll-view
        v-if="!isInit && list.length"
        ref="scroll"
        class="scroll-view"
        :data="list"
        :pullup="pullup"
        :pulldown="pulldown"
        @pullUpLoad="listMore"
        @pullDownRefresh="listRefresh">
        <div class="scroll-view-container">
          <div class="messages-list">
            <div class="scroll-view-pulldown">下拉刷新...</div>
            <div class="messages-item" v-for="(item, index) in list" :key="index">
              <div class="messages-item-title">{{ item.subject }}<span v-if="item.isread === 1" class="unread"></span></div>
              <div class="messages-item-desc">{{ item.body }}</div>
              <div class="messages-item-time">{{ item.sendtime }}</div>
            </div>
            <div class="scroll-view-pullup">{{ isMore ? '加载中...' : '目前就这么多了' }}</div>
          </div>
        </div>
      </pem-scroll-view>
      <div v-if="!isInit && !list.length" class="empty-result">
        <div class="empty-result-icon icon-system"></div>
        <span>暂无系统信息</span>
      </div>
      <pem-operation-panel @backtotop="backtotop" />
      <pem-loading v-show="isLoading"></pem-loading>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
import PemOperationPanel from 'components/pem-operation-panel/pem-operation-panel'
import PemScrollView from 'components/scroll-view/scroll-view'
import PemLoading from 'components/pem-loading/pem-loading'
import { MiniSiteApi } from 'api'
import { dateFormat, setTitle } from 'common/js/util'

export default {
  components: {
    PemOperationPanel,
    PemScrollView,
    PemLoading
  },
  data () {
    return {
      pullup: true,
      pulldown: true,
      list: [],
      currentPage: 1,
      pageSize: 10,
      isSetReadSysMsg: false,
      isLoading: true,
      isInit: true,
      isMore: true
    }
  },
  created () {
    this._initWXShare()
    this._setTitle('项目工场-系统消息')
    setTimeout(() => {
      this._initData()
    }, 20)
  },
  methods: {
    _initWXShare () {
      this.wxShare({
        title: '为您推荐项目工场',
        content: '我正在使用项目工场，有料的BP、有趣的VC都在这儿，一起来看项目吧！',
        linkUrl: window.location.origin
      })
    },
    _setTitle (title) {
      setTitle(title)
    },
    _initData () {
      this.getUserSysMsglist('init', () => {
        this.isInit = false
      })
    },
    backtotop () {
      this.$refs.scroll.scrollTo(0, 0, 300)
    },
    listRefresh () {
      if (!this.isLoading) {
        this.isMore = true
        this.getUserSysMsglist('init')
      }
    },
    listMore () {
      if (!this.isLoading && this.isMore) {
        this.getUserSysMsglist('more')
      }
    },
    getUserSysMsglist (type = 'init', callback) {
      this.isLoading = true
      if (type === 'init') {
        this.currentPage = 1
      }
      this._userSysMsglist({
        currentPage: +this.currentPage,
        pageSize: +this.pageSize
      }, (data) => {
        if (+data.resultcode === 1) {
          this.currentPage = (+data.currentPage || 1) + 1
          if (data.totalPage <= +data.currentPage) {
            this.isMore = false
          }
          let _list = (type === 'init') ? [] : this.list
          this.list = _list.concat(data.resultList || []).map(item => {
            item.sendtime = item.sendtime ? dateFormat(item.sendtime, 'yyyy-MM-dd hh:mm') : ''
            return item
          })
          this.isLoading = false
          this.list.forEach(item => {
            if (item.isread === 1) {
              this.isSetReadSysMsg = true
            }
          })
          if (this.list.length > 0 && this.isSetReadSysMsg) {
            this.setReadSysMsg()
          }
          callback && callback()
        }
      })
    },
    setReadSysMsg () {
      this._setReadSysMsg()
    },
    _userSysMsglist (options, callback) {
      MiniSiteApi.userSysMsglist(options).then(data => {
        callback(data)
      })
    },
    _setReadSysMsg () {
      MiniSiteApi.setReadSysMsg()
    }
  }
}
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/variable"
  @import "~common/stylus/mixin"
  .messages
    font-size: 0.24rem
    .messages-list
      padding-left: 0.3rem
      background: $color-panel
      .messages-item
        position: relative
        line-bottom()
        padding-top: 0.3rem
        padding-bottom: 0.3rem
        padding-right: 0.3rem
        .messages-item-title
          font-size: 0.32rem
          line-height: 0.44rem
          color: #222222
          margin-bottom: 0.1rem
          .unread
            width: 6px
            height: 6px
            border-radius: 6px
            background-color: #eb4b36
            display: inline-block
            vertical-align: text-top
        .messages-item-desc
          font-size: 0.28rem
          line-height: 0.34rem
          color: #A7A5A3
        .messages-item-time
          font-size: 0.24rem
          line-height: 0.34rem
          color: #A7A5A3
          padding-top: 0.1rem
    .empty-result
      font-size: 0
      text-align: center
      padding-top: 0.6rem
      height: 100%
      background-color: #fff
      .empty-result-icon
        display: inline-block
        width: 3.56rem
        height: 3.56rem
        background-size: 3.56rem 3.56rem
        &.icon-system
          bg-image('icon-System message-Empty state')
      span
        display: block
        font-size: 0.32rem
        color: $color-text-title
        line-height: 0.44rem
</style>



